const todoBox = document.querySelector('.todoBox');

function getTime() {
  const time = document.createElement('span');
  let hours = new Date().getHours();
  let minutes = new Date().getMinutes();
  let AmOrPm = hours >= 12 ? 'pm' : 'am';

  hours = hours % 12 || 12;
  hours = hours < 10 ? '0' + hours : hours;
  minutes = minutes < 10 ? '0' + minutes : minutes;
  time.innerHTML = hours + ':' + minutes + AmOrPm;
  return time;
}

function makeTodoList(state) {
  const todobox = document.createElement('div');
  const todo = document.createElement('span');
  const doneBnt = document.createElement('button');
  const delBnt = document.createElement('button');
  const now = getTime();
  todo.innerText = state.item;
  doneBnt.innerText = state.check;
  delBnt.innerText = state.delete;
  delBnt.id = state.id;
  todobox.append(todo);
  todobox.append(now);
  todobox.append(doneBnt);
  todobox.append(delBnt);
  todoBox.prepend(todobox);
}

export { makeTodoList };
